import React, { useEffect, useState, useCallback } from 'react';
import {
  // Box,
  // Button,
  // Card,
  // CardActions,
  // CardContent,
  // CardHeader,
  // Container,
  Grid,
  // Typography
} from '@material-ui/core';
// import {
//   OverviewInbox,
//   OverviewLatestTransactions,
//   OverviewPrivateWallet,
//   OverviewTotalBalance,
//   OverviewTotalTransactions,
//   OverviewWeeklyEarnings,
// } from '../overview';
// import useSettings from "../../hooks/useSettings";
import QuickStats2 from '../../widgets/quick-stats/QuickStats2';
import gtm from '../../../lib/gtm';
import OrderRepository from '../../../repositories/order.repository';
import OrderListTable from './orders/OrderListTable';
import useMounted from '../../../hooks/useMounted';

const OverviewStore = () => {
  const [isLoading, setIsLoading] = useState(false);
  const orderRepository = new OrderRepository();
  const [orders, setOrders] = useState([]);
  const mounted = useMounted();
  // const { settings } = useSettings();
  useEffect(() => {
    gtm.push({ event: 'page_view' });
    setIsLoading(false);
  }, []);
  const getOrders = useCallback(async () => {
    setIsLoading(true);
    try {
      const data = await orderRepository.getAllOrders();
      console.log(data);
      setOrders(data);
      if (mounted.current) {
        console.log(data);
        setOrders(data);
      }
      setIsLoading(false);
    } catch (err) {
      console.error(err);
    }
  }, [mounted]);

  useEffect(() => {
    getOrders();
  }, [getOrders]);

  return (
    <>
      {!isLoading && (
        <Grid
          item
          md={12}
          xs={12}
        >
          <QuickStats2 />
        </Grid>
      )}
      <Grid
        item
        md={12}
        xs={12}
      >
        {!isLoading && <OrderListTable orders={orders} />}
      </Grid>
    </>
  );
};

export default OverviewStore;
