import { useEffect } from 'react';
import { Helmet } from 'react-helmet-async';
import {
  Box,
  Button,
  // Card,
  // CardActions,
  // CardContent,
  // CardHeader,
  Container,
  Grid,
  Typography
} from '@material-ui/core';
import useSettings from '../../hooks/useSettings';
import PlusIcon from '../../icons/Plus';
import gtm from '../../lib/gtm';
import OverviewAdmin from '../../components/dashboard/admin/OverviewAdmin';
import OverviewStore from '../../components/dashboard/store/OverviewStore';
import { Link as RouterLink } from 'react-router-dom';

const Overview = () => {
  // const [isLoading, setIsLoading] = useState(false);
  const { settings } = useSettings();
  const rank = localStorage.getItem('rank_id');
  let textButton = '';
  if (rank === 'ADM') {
    textButton = 'Nueva Tienda';
  } else if (rank === 'STR') {
    textButton = 'Crear Orden';
  }
  useEffect(() => {
    gtm.push({ event: 'page_view' });
    // setIsLoading(false);
  }, []);

  return (
    <>
      <Helmet>
        <title>Dashboard: Resumen | Mac Admin</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: 'background.default',
          minHeight: '100%',
          py: 8
        }}
      >
        <Container maxWidth={settings.compact ? 'xl' : false}>
          <Grid
            container
            spacing={3}
          >
            <Grid
              alignItems="center"
              container
              justifyContent="space-between"
              spacing={3}
              item
              xs={12}
            >
              <Grid item>
                <Typography
                  color="textSecondary"
                  variant="overline"
                >
                  Resumen
                </Typography>
                <Typography
                  color="textPrimary"
                  variant="h5"
                >
                  Buenos dias
                  {' '}
                  {localStorage.getItem('name')}
                </Typography>
                <Typography
                  color="textSecondary"
                  variant="subtitle2"
                >
                  Aquí encontraras un resumen del día de hoy
                </Typography>
              </Grid>
              <Grid item>
                <Button
                  color="primary"
                  startIcon={<PlusIcon fontSize="small" />}
                  variant="contained"
                  component={RouterLink}
                  to="/dashboard/store/order/create"
                >
                  { textButton }
                </Button>
              </Grid>
            </Grid>
            { rank === 'ADM' && <OverviewAdmin /> }
            { rank === 'STO' && <OverviewStore /> }
          </Grid>
        </Container>
      </Box>
    </>
  );
};

export default Overview;
